.featured-outer{
  position: relative;
  height: 100%;
  margin: 0;
  padding: 0;
  &.small {
    height: 12em;
  }
}
.featured-wrapper{
  position: relative;
  height: 100%;
  padding: 120px 0;
  color: #eee;
  text-shadow: 0 0 2px rgba(0,0,0,0.5);
  background-color: rgba(0,0,0,0.9);
  // @include breakpoint( $max-phone ) {
  //   height: 50%;
  // }
  .carousel, .carousel-inner, .carousel-item {
    height: 100%;
  }
  .sharedaddy {
    display: none !important;
  }
  .caption, .description, .excerpt {
    text-align: justify;
  }
  .media {
    max-width: 100%;
    margin: 0 auto;
    height: 100%;
  }
  .media-wrapper {
    padding: 0;
  }
}
#featured {
  height: 100%;
  .VueCarousel-wrapper, .VueCarousel-inner {
    position: relative;
    height: 100% !important;
  }
  .VueCarousel-slide > .container,
  .featured-wrapper > .container {
    position: relative;
    height: 100%;
    > .row {
      position: relative;
      height: 100%;
      > .col {
        position: relative;
        height: 100%;
      }
      @include breakpoint( $max-phone ) {
        &.media-wrapper {
          height: 70%;
        }
        &.col-md-4 {
          height: 30%;
        }
      }
    }
  }
}
